import { CardMedia, CardText, CardTitle, CardHeader } from 'material-ui/Card';
import { GlassCard, VerticalCanvas, FullPageCanvas, Glass, DynamicSpacer } from 'meteor/clinical:glass-ui';
import { Col, Grid, Row } from 'react-bootstrap';

import React from 'react';
import { ReactMeteorData } from 'meteor/react-meteor-data';
import ReactMixin from 'react-mixin';
import { browserHistory } from 'react-router';

import { get, has, uniq, uniqBy } from 'lodash';
import { moment } from 'meteor/momentjs:moment';

import { Session } from 'meteor/session';
import { ObservationsTable } from 'meteor/clinical:hl7-resource-observation';
import { Line } from 'nivo'
import { render } from 'react-dom'

import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

import faker from 'faker';

// import { InlineMath, BlockMath } from 'react-katex';
// import 'katex/dist/katex.min.css';
Session.setDefault('currentObservationType', 'Pulse')
Session.setDefault('patientLayout', 'quad');

export class BiometricsDashboardPage extends React.Component {
  constructor(props) {
    super(props);
  }
  getMeteorData() {

    let imgHeight = (Session.get('appHeight') - 210) / 3;

    let data = {
      chart: {
        width: Session.get('appWidth') * 0.667,  
        height: Session.get('appHeight') - 168
      },
      style: {              
        title: Glass.darkroom(),
        subtitle: Glass.darkroom(),
        tableCol: {
          display: 'block',
          marginBottom: '20px'
        }
      },
      uniqueCodes: [],
      currentObservationType: Session.get('currentObservationType'),
      cohort: [{
        // id: 'GeQjtdXb8qLw6CMpd',
        title: faker.name.findName(),   
        subtitle: '36yr F',             
        patientReference: 'Patient/1'
      }, {
        // id: 'Ht3s9fWtdwjqxCwte',
        title: faker.name.findName(),
        subtitle: '31yr M',
        patientReference: 'Patient/Anyman/Bob'
      }, {
        // id: 'Ht3s9fWtdwjqxCwte',
        title: faker.name.findName(),
        subtitle: '48yr F',
        patientReference: 'Patient/Smart/Nancy'
      }, {
        // id: 'Ht3s9fWtdwjqxCwte',
        title: faker.name.findName(),
        subtitle: '22yr M',
        patientReference: 'Patient/Argonaut/Jason'
      }],
      biomarkerSwimlanes: 1
    };

    var navHeight = 40;
    if(Session.get('showNavbars')){
      navHeight = 168;
    }

    switch (Session.get('patientLayout')) {
      case 'oneup':
        data.chart.height = Session.get('appHeight') - navHeight;
        data.biomarkerSwimlanes = 1;
        break;
      case 'twoup':
        var n = 2
        data.chart.height = (Session.get('appHeight') - navHeight - ((n - 1) * 20)) / n;      
        data.biomarkerSwimlanes = 2;
      break;
      case 'threeup':
        var n = 3
        data.chart.height = (Session.get('appHeight') - navHeight - ((n - 1) * 20)) / n;      
        data.biomarkerSwimlanes = 3;
      break;
      case 'quad':
        var n = 4;
        data.chart.height = (Session.get('appHeight') - navHeight - ((n - 1) * 20)) / n;      
        data.biomarkerSwimlanes = 4;
      break;
      case 'octet':
        var n = 8;
        data.chart.height = (Session.get('appHeight') - navHeight - ((n - 1) * 20)) / n;      
        data.biomarkerSwimlanes = 8;
      break;    
      default:
        break;
    }  



    data.style.indexCard = Glass.darkroom(data.style.indexCard);

    // The `_.property` iteratee shorthand.
    // _.uniqBy([{ 'x': 1 }, { 'x': 2 }, { 'x': 1 }], 'x');
    // => [{ 'x': 1 }, { 'x': 2 }]

    let observations = Observations.find().map(function(record){
      return get(record, 'code.text');
    })
    if(observations){
      let uniqueCodes = uniq(observations)
      //console.log('Unique Observation Codes: ', uniqueCodes)

      data.uniqueCodes = uniq(observations);
    }

    if(Session.get('appHeight') > 1200){
      data.style.page = {
        top: '50%',
        transform: 'translateY(-50%)',
        position: 'relative'
      }
    }

    if(Observations.find({'code.text': data.currentObservationType }).count() > 0){
      let recentPulse = Observations.find({'code.text': data.currentObservationType }, {sort: {issued: 1}}).fetch()[0];
      // data.bmi.weight = get(recentPulse, 'valueQuantity.value', 0);
    }

    if(Session.get('appWidth') > Session.get('appHeight')){
      data.chart.width = Session.get('appWidth');
    }

    if(['iPhone'].includes(window.navigator.platform)){
      data.style.tableCol.display = 'none';
      data.chart.width = Session.get('appWidth');
    }

    console.log("Navigated to BiometricsDashboardPage", data);
    return data;
  }
  selectObservationType(event, value){
    console.log('Selected a new observation type: ', value.props.children)
    Session.set('currentObservationType', value.props.children)
  };
  render() {

    let self = this;

    let menuItems = [];
    this.data.uniqueCodes.forEach(function(code, integer){
      menuItems.push(<MenuItem key={integer} value={integer} style={{fontSize: '130%'}}>{code}</MenuItem>)
    });

    let currentSelectIndex = this.data.uniqueCodes.indexOf(get(this, 'data.currentObservationType'))

    var biometricSwimlanes = [];

    console.log('this.data.biomarkerSwimlanes', this.data.biomarkerSwimlanes)


    this.data.cohort.forEach(function(patient, index){
      biometricSwimlanes.push(<GlassCard key={index} height={get(self, 'data.chart.height') + 'px'} style={{marginBottom: '20px'}}>
          <CardTitle title={ get(patient, 'title')} subtitle={ get(patient, 'subtitle') } />
          <Line
            width={ get(self, 'data.chart.width') }
            height={ get(self, 'data.chart.height') - 88 }
            curve='cardinal'
            // colorBy="color"
            colors={['#35B6ED']}
            data={[
              {
                "id": get(self, 'data.currentObservationType'),
                "color": "#35B6ED",
                "data": Observations.find({$and: [
                    {'subject.reference': get(patient, 'patientReference')},
                    {'code.text': get(self, 'data.currentObservationType')}
                  ]}, {sort: {issued: 1}}).map(function(observation, index){
                  return {
                    key: index,
                    // color: "#35B6ED",
                    x: moment(get(observation, 'issued')).format('MMM DD, HH'),
                    y: get(observation, 'valueQuantity.value')
                  }
                })
              }
            ]}
            margin={{
              "top": 0,
              "right": 110,
              "bottom": 50,
              "left": 60
            }}
            minY="auto"
            stacked={true}
            axisBottom={{
              "orient": "bottom",
              "tickSize": 5,
              "tickPadding": 5,
              "tickRotation": 0,
              // "legend": "hour of day",
              "legendOffset": 36,
              "legendPosition": "center"
            }}
            axisLeft={{
              "orient": "left",
              "tickSize": 5,
              "tickPadding": 5,
              "tickRotation": 0,
              // "legend": "pulse (bps)",
              "legendOffset": -40,
              "legendPosition": "center"
            }}
            dotSize={10}
            dotColor="#35B6ED"
            dotBorderWidth={2}
            dotBorderColor="#ffffff"
            enableDotLabel={true}
            dotLabel="y"
            dotLabelYOffset={-12}
            animate={true}
            motionStiffness={90}
            motionDamping={15}
            legends={[
              {
                "anchor": "bottom-right",
                "direction": "column",
                "translateX": 100,
                "itemWidth": 80,
                "itemHeight": 20,
                "symbolSize": 12,
                "symbolShape": "circle"
              }
            ]}
          />
          <DynamicSpacer />
        </GlassCard>
      )

    });
    
    return (
      <div id='indexPage'>
        <FullPageCanvas>
          {/* <Col md={4} style={ this.data.style.tableCol } >
            <GlassCard height='auto' >
              <CardTitle 
                title="Vital Signs"
                titleStyle={{fontSize: '240%'}}
                subtitleStyle={{fontSize: '180%'}}
                />
              <Select
                value={ currentSelectIndex }
                onChange={this.selectObservationType}
                style={{
                  position: 'absolute',     
                  right: '20px',
                  top: '20px',
                  marginRight: '20px',
                  width: '240px',
                  fontSize: '150%'
                }}
                inputProps={{
                  name: 'age',
                  id: 'age-simple',
                }}
              >
                { menuItems }
              </Select>             
              <CardText style={{fontSize: '180%'}}>
                <Row>
                  <ObservationsTable 
                    query={{$or: [{'code.text': get(this, 'data.currentObservationType')}]}} 
                    multiline={true}
                    />
                </Row>
              </CardText>
            </GlassCard>
          </Col> */}
          <Col md={12} style={{marginBottom: '80px'}}>
            { biometricSwimlanes }
          </Col>
        </FullPageCanvas>
      </div>
    );
  }

  openLink(url){
    console.log("openLink", url);
    browserHistory.push(url);
  }
}



ReactMixin(BiometricsDashboardPage.prototype, ReactMeteorData);

export default BiometricsDashboardPage;