      //     text: get(Meteor.user(), 'profile.name.given') + ' ' + get(Meteor.user(), 'profile.name.family')
      //   }]
      // } else {
      //   data.user.given = '';
      //   data.user.family = '';
      //   data.user.fullName = '';
      //   data.patient.name = [{
