var callback = function (msg) {
  // wrapping in a timeout because of a possbile native UI element blocking the webview
  setTimeout(function () {
    // alert(JSON.stringify(msg))
    console.log(JSON.stringify(msg))
  }, 0);
};


Session.set('useMinimongoCache', true);


export class ContinuityOfCarePage extends React.Component {
  constructor(props) {
    super(props);
  }
  getMeteorData() {
    let data = {
      style: {},
      ccd: {
        allergyIntolerances: [],
        careplans: [],
        conditions: [],
        diagnosticReports: [],
        immunizations: [],
        medications: [],
        medicationOrders: [],
        medicationStatements: [],
        observations: [],
        patients: [],
        procedures: []
      },
      minimongo: {
        allergyIntolerances: [],
        careplans: [],
        conditions: [],
        diagnosticReports: [],
        immunizations: [],
        medications: [],
        medicationOrders: [],
        medicationStatements: [],
        observations: [],
        patients: [],
        procedures: []
      },
      query: {},
      hideEnteredInError: Session.get('hideEnteredInError'),
      hidePatientName: Session.get('hidePatientName'),
      hideToggles: Session.get('hideToggles'),
      hideActionIcons: Session.get('hideActionIcons'),
      hideIdentifiers: Session.get('hideIdentifiers')
    };

    if(['iPhone'].includes(window.navigator.platform)){
      data.hideActionIcons = true;
    }
    
    if(Meteor.user()){

      var filters = get(Meteor.user(), 'profile.filters');

      if(filters){
        if(filters.remove && filters.remove.length > 0){
          data.query.$nor = [];
          filters.remove.forEach(function(term){
            if(term){
              data.query.$nor.push({ "code.text": { "$regex": "^" + term, "$options": "i" } })
            }
          });  
        }
  
        if(filters.mustHave && filters.mustHave.length > 0){
          data.query.$or = [];
          filters.mustHave.forEach(function(term){
            if(term){
              data.query.$or.push({ "code.text": { "$regex": "^" + term, "$options": "i" } })
            }
          })          
        }

        if(get(filters, 'sensitiveItems.substanceAbuse')){
          if(!data.query.$nor) data.query.$nor = [];
          // Additional info:
          // http://www.healthvermont.gov/sites/default/files/documents/2016/11/ADAP_ICD-10_ADAP_Approved_Conversion_Codes.pdf
          data.query.$nor.push({ "code.text": { "$regex": "^alcohol", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^cocain", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^cocaine", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^opioid", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^hallucinogen", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^psychoactive substance", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^cannabis", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^abuse", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^dependence", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^intoxication", "$options": "i" } })
        }

        if(get(filters, 'sensitiveItems.mentalHealth')){
          if(!data.query.$nor) data.query.$nor = [];
          data.query.$nor.push({ "code.text": { "$regex": "^anxiety", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^adjustment disorder", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^post-traumatic stress disorder", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^depressive", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^disorder", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^personality", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^phobia", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^obsessional", "$options": "i" } })

          // TODO: filter explicit codes
          // https://www.simplepractice.com/blog/top-icd-10-codes-behavioral-health-2017/  
        }
        if(get(filters, 'sensitiveItems.sexualHealth')){
          if(!data.query.$nor) data.query.$nor = [];
          data.query.$nor.push({ "code.text": { "$regex": "^neisseria", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^gonorrhoeae", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^gonnorrhoeae", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^gonnorrhea", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^chlamydia", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^syphilis", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^hepatitis", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^eGFR", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^HIV", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^AIDS", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^herpes", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^crabs", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^chlamydia", "$options": "i" } })

          data.query.$nor.push({ "code.text": { "$regex": "^estrogen", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^estradiol", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^testosterone", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^progesterone", "$options": "i" } })

          // TODO:  filter explicit codes
          // https://www.aapc.com/blog/32415-sti-screening-under-medicare/
        }
      }

      
      if(typeof AllergyIntolerances === "object"){
        data.ccd.allergyIntolerances = AllergyIntolerances.find().fetch();
      }
      if(typeof CarePlans === "object"){
        data.ccd.carePlans = CarePlans.find().fetch();
      }
      if(typeof Conditions === "object"){
        let conditionsQuery = {};
        if(data.query){
          conditionsQuery = data.query;
        }
        if(Session.get('hideEnteredInError')){          
          conditionsQuery.verificationStatus = {$nin: ["entered-in-error"]}  // unconfirmed | provisional | differential | confirmed | refuted | entered-in-error
        }
        data.ccd.conditions = Conditions.find(conditionsQuery).fetch();
      }
      if(typeof DiagnosticReports === "object"){
        data.ccd.diagnosticReports = DiagnosticReports.find(data.query).fetch();
      }
      if(typeof Immunizations === "object"){
        data.ccd.immunizations = Immunizations.find().fetch();
      }
      if(typeof Medications === "object"){
        data.ccd.medications = Medications.find().fetch();
      }
      if(typeof Medications === "object"){
        data.ccd.medicationOrders = MedicationOrders.find().fetch();
      }        
      if(typeof MedicationStatements === "object"){
        data.ccd.medicationStatements = MedicationStatements.find().fetch();
      }
      if(typeof Patients === "object"){
        data.ccd.patients = Patients.find().fetch();
      }
      if(typeof Observations === "object"){
        data.ccd.observations = Observations.find(data.query).fetch();
      }
      if(typeof Procedures === "object"){
        data.ccd.procedures = Procedures.find(data.query).fetch();
      }  
    }

    console.log('Navigated to ContinuityOfCarePage', data);
    return data;
  }
  openLink(url){
    browserHistory.push(url);
  }
  openFastImport(){
    browserHistory.push('/fast-import-chart')
  }
  openSignup(){
    browserHistory.push('/signup')    
  }
  deduplicate(collectionName){
    console.log('deduplicate', collectionName)    
  }  
  importHealthRecord(){
    MedicalRecordImporter.healthRecord();
  }
  render(){

    var allergiesCard;
    var carePlansCard;
    var conditionsCard;
    var diagnosticReportsCard;
    var immunizationsCard;
    var medicationsCard;
    var medicationOrdersCard;
    var medicationStatementsCard;
    var observationsCard;
    var proceduresCard;
    var patientsCard;

    var chartIsEmpty = true;

    let showIdentifier = true;


    if(Patients.find().count() > 0){
      chartIsEmpty = false;
      patientsCard = <div>
        <GlassCard >
          <CardTitle title="Patients" />
          <CardText>
            <PatientsTable
              hideActionIcons={this.data.hideActionIcons}
              data={ this.data.ccd.patients } 
              hideCheckboxes={this.data.hideToggles}
            />            
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/patients') } />
          </CardActions> */}
        </GlassCard> 
        <DynamicSpacer />
      </div>      
    }


    if(AllergyIntolerances.find().count() > 0){
      chartIsEmpty = false;
      allergiesCard = <div>
        <GlassCard>
          <CardTitle title="Allergies" />
          <CardText>
            <AllergyIntolerancesTable
              data={ this.data.ccd.allergyIntolerances } 
              hideIdentifier={this.data.hideIdentifiers}
              hideCategory={true}
              displayDates={false}
              hidePatient={this.data.hidePatientName} 
              hideEnteredInError={true}
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />
        </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/allergies') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Allergies') } />
          </CardActions> */}
        </GlassCard>   
        <DynamicSpacer />
      </div>
    }

    if(CarePlans.find().count() > 0){
      chartIsEmpty = false;
      carePlansCard = <div>
        <GlassCard>
          <CardTitle title="CarePlans" />
          <CardText>
            <CarePlansTable
              data={ this.data.ccd.carePlans } 
              hideSubject={this.data.hidePatientName}
              displayDates={true} 
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />                  
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/care-plans') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'CarePlans') } />
          </CardActions> */}
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    if(Conditions.find().count() > 0){
      chartIsEmpty = false;
      conditionsCard = <div>
        <GlassCard>
          <CardTitle title="Conditions" />
          <CardText>
            <ConditionsTable
              data={ this.data.ccd.conditions } 
              hideIdentifier={this.data.hideIdentifiers}
              hideEvidence={true}
              hideServerity={true}
              hideEnteredInError={true}
              hideAsserterName={true}
              hidePatientName={this.data.hidePatientName}
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />                  
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/conditions') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Conditions') } />
          </CardActions> */}
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    if(DiagnosticReports.find().count() > 0){
      chartIsEmpty = false;
      diagnosticReportsCard = <div>
        <GlassCard>
          <CardTitle title="Diagnostic Reports" />
          <CardText>
            <DiagnosticReportsTable 
              data={ this.data.ccd.diagnosticReports } 
              hidePatientName={this.data.hidePatientName}
              displayDates={true} 
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />
            
          </CardText>
          {/* <CardActions>       
          <FlatButton label='Add' onClick={ this.openLink.bind(this, '/diagnostic-reports') } />
          <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'DiagnosticReports') } />
          </CardActions> */}
        </GlassCard>  
        <DynamicSpacer />
      </div>
    }
    if(Immunizations.find().count() > 0){
      chartIsEmpty = false;
      immunizationsCard = <div>
        <GlassCard>
          <CardTitle title="Immunizations" />
          <CardText>
            <ImmunizationsTable 
              data={ this.data.ccd.immunizations } 
              hidePatientName={this.data.hidePatientName}
              displayDates={true} 
              hideCheckboxes={this.data.hideToggles}
              hideIdentifier={this.data.hideIdentifiers}
              hidePatient={this.data.hidePatientName}
              hidePerformer={true}
              hideActionIcons={this.data.hideActionIcons}
            />
          </CardText>
          {/* <CardActions>     
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/immunizations') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Immunizations') } />
          </CardActions> */}
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    // if(Medications.find().count() > 0){
    //   medicationsCard = 
    // }
    if(MedicationOrders.find().count() > 0){
      chartIsEmpty = false;
      medicationOrdersCard = <div>
        <GlassCard>
          <CardTitle title="Medication Orders" />
          <CardText>
            <MedicationOrdersTable
              medicationOrders={ this.data.ccd.medicationOrders } 
              hidePatientName={this.data.hidePatientName}
              hidePrescriberName={true}
              displayDates={true} 
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />
            
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/medications-orders') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'MedicationOrders') } />
          </CardActions> */}
        </GlassCard>          
        <DynamicSpacer />
      </div>      
    }
    if(MedicationStatements.find().count() > 0){
      chartIsEmpty = false;
      medicationStatementsCard = <div>
        <GlassCard>
          <CardTitle title="Medication Statements" />
          <CardText>
            <MedicationStatementsTable
              data={ this.data.ccd.medicationStatements } 
              hidePatientName={this.data.hidePatientName}
              hidePrescriberName={true}
              displayDates={true} 
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />
            
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/medication-statements') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'MedicationStatements') } />
          </CardActions> */}
        </GlassCard>
        <DynamicSpacer />
      </div>
    }
    if(Observations.find().count() > 0){
      chartIsEmpty = false;
      observationsCard = <div>
        <GlassCard>
          <CardTitle title="Observations" />
          <CardText>
            <ObservationsTable
              observations={ this.data.ccd.observations } 
              hideBarcodes={true}
              hideValue={true}
              hideComparator={true}
              hideSubjects={true}
              hidePatientName={this.data.hidePatientName}
              multiline={true}
              query={this.data.query}
              onRemoveRecord={function(_id){
                Observations._collection.remove({_id: _id})
              }}
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />
            
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/observations') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Observations') } />
          </CardActions> */}
        </GlassCard>
        <DynamicSpacer />
      </div>        
    }
    if(Procedures.find().count() > 0){
      chartIsEmpty = false;
      proceduresCard = <div>
        <GlassCard>
          <CardTitle title="Procedures" />
          <CardText>
            <ProceduresTable
              data={ this.data.ccd.procedures } 
              hideIdentifier={this.data.hideIdentifiers}
              hideSubject={this.data.hidePatientName}
              hideCategory={true}
              hidePerformer={true}
              hideBodySite={true}
              displayDates={true} 
              hideCheckboxes={this.data.hideToggles}
              hideActionIcons={this.data.hideActionIcons}
            />
            
          </CardText>
          {/* <CardActions>       
            <FlatButton label='Add' onClick={ this.openLink.bind(this, '/procedures') } />
            <FlatButton label='Deduplicate' onClick={ this.deduplicate.bind(this, 'Procedures') } />
          </CardActions> */}
        </GlassCard> 
        <DynamicSpacer />
      </div>      
    }
    

    var importWidget;
    var chart;
    var importStyle = {
      textAlign: 'center',
      position: 'relative',
      top: ((Session.get('appHeight') - 128 ) * 0.5) - 120 + 'px'
    }

    if(Meteor.userId()){
      if(chartIsEmpty){
        if(Meteor.isCordova){
          if(['iPhone'].includes(window.navigator.platform)){
            let user = Meteor.user();
            if(get(user, 'profile.needHealthRecordsAuth') === true){
              importWidget = <Grid>
                <Row style={importStyle}>
                  <h1 style={{fontWeight: '200'}}>No health data found.</h1>
                  <RaisedButton label="Authorize Health Record Access" onClick={ this.importHealthRecord } primary={true} />
                </Row>
              </Grid>  

            } else {
              importWidget = <Grid>
                <Row style={importStyle}>
                  <h1 style={{fontWeight: '200'}}>Authorization started.</h1>
                  <RaisedButton label="Import Health Records" onClick={ this.importHealthRecord } primary={true} />
                </Row>
              </Grid>  
            }
          }
        } else {
          importWidget = <Grid>
          <Row style={importStyle}>
            <h1 style={{fontWeight: '200'}}>No health data found.</h1>
            <RaisedButton label="Import Medical Charts" onClick={this.openFastImport } primary={true} />
          </Row>
        </Grid>  
        }
      } else {
        chart = <div>
        { allergiesCard }
        { carePlansCard }                        
        { conditionsCard }                        
        { diagnosticReportsCard }
        { immunizationsCard }
        { medicationOrdersCard }
        { observationsCard }
        { patientsCard }
        { proceduresCard }
        </div>
      }
    } else {
      importWidget = <Grid>
        <Row style={importStyle}>
          <h1 style={{fontWeight: '200'}}>Welcome to Timelines.</h1>
          <RaisedButton label="Register" onClick={this.openSignup } primary={true} />
        </Row>
      </Grid>  
    }



    return(
      <div id="ContinuityOfCarePage">
        <FullPageCanvas >
          <Grid>
            <Row>
              <Col>
                { importWidget }
                { chart }
              </Col>
            </Row>
          </Grid>
        </FullPageCanvas>
      </div>
    );
  }
}
ReactMixin(ContinuityOfCarePage.prototype, ReactMeteorData);
export default ContinuityOfCarePage;


