import { Session } from 'meteor/session';
import { Table } from 'react-bootstrap';

import { FullPageCanvas, VerticalCanvas, DynamicSpacer, GlassCard, Glass } from 'meteor/clinical:glass-ui';
import { parseString } from 'xml2js';
import { browserHistory } from 'react-router';
import { get } from 'lodash';

import { ImportComponent } from './ImportComponent';

export class ImportPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      importValue: 'importTab',
      exportValue: 'exportTab',
      encryptExport: false
    };
  }


  getMeteorData() {
    let data = {
      style: {
        opacity: Session.get('globalOpacity'),
        tab: {
          borderBottom: '1px solid lightgray',
          borderRight: 'none'
        }
      },
      dialog: {
        open: Session.get('open')
      },      
      user: {
        isAdmin: false
      },
      title: "Client Collections",
      upstreamSync: false,
      encryptExport: this.state.encryptExport,
      import: {
        fileExtension: Session.get('fileExtension'),
        // height: (Session.get('appHeight') - 540 ) + 'px',
        height: (Session.get('appHeight') - 556 ) + 'px',  //565
        data: ''
      },
      dataManagement: {
        height: (Session.get('appHeight') - 300 ) + 'px'
      },
      export: {
        height: (Session.get('appHeight') - 380 ) + 'px',
        data: ''
      },
      syncSourceItem: Session.get('syncSourceItem'),
      toggleStates: Session.get('toggleStates')
    };


    if(Session.get('continuityOfCareDoc')){
      data.export.data = JSON.stringify(Session.get('continuityOfCareDoc'), null, 2);
    }

    data.style = Glass.blur(data.style);
    data.style.tab = Glass.darkroom(data.style.tab);

    console.log('Navigated to ImportPage', data);
    return data;
  }
   
  render(){
    return(
      <div id="ImportPage">
        <FullPageCanvas >
          <ImportComponent />
        </FullPageCanvas>
      </div>
    );
  }

}


ReactMixin(ImportPage.prototype, ReactMeteorData);
export default ImportPage;