export class TimelineSidescrollPage extends React.Component {
  constructor(props) {
    super(props);
  }
  componentWillMount(){
    const timeLineEvents = {};
    // this is the timeline that initially renders
    // the one problem is that it's going to use the current screen height/width 
    this.timeline = (<Timeline 
        id="horizontalTimeline"
        selectHandler={ this.openDialog }
        changedHandler={this.preventRedraw }
        clickHandler={this.preventRedraw }
        options={this.data.options} 
        items={this.data.items} 
        groups={this.data.groups} 
        events={timeLineEvents}
        />)
  }
  getMeteorData() {
    var currentPatient;

    // is a patient associated with the logged in user profile?
    if(get(Meteor.user(), 'profile.patientId')){
      currentPatient = Patients.findOne({_id: get(Meteor.user(), 'profile.patientId')})
    }

    // no?  well, let's use the first patient in the Patient's cursor
    if(!currentPatient){
      currentPatient = Patients.findOne();
    }


    let data = {
      timelineDialogOpen: Session.get('timelineDialogOpen'),
      options: {
        width: '100%',
        // width: window.innerWidth + 'px',
        // width: Session.get('appWidth'),
        autoResize: true,
        height: (Session.get('appHeight') - 128),
        // height: window.innerHeight - 128 + 'px',
        dataAttributes: ['id', 'mongoId'],
        stack: true,
        showMajorLabels: false,
        showCurrentTime: true,
        zoomMin: 1000000,
        format: {
          // ??
          // ??
        },

        start: '1978-01-25',  // birthdate
        end: moment().format("YYYY-MM-DD"), //now
        min: '1977-04-25',  // birthdate - 9mo
        max: '2050-01-01',  // birthdate +76 yrs

        
        // start: '1978-01-25',  // birthdate
        // end: moment().format("YYYY-MM-DD"), //now
        // min: '1977-04-25',  // birthdate - 9mo
        // max: '2050-01-01',  // birthdate +76 yrs

        // // TODO:  Clinicals on FHIR Usecase
        // // Need to generalize the timeline function instead of hardcoding examples
        // start: '1990-07-04',
        // end: moment().format("YYYY-MM-DD"),
        // min: '1989-10-04',
        // max: '2066-07-04', 

        groupOrder: 'content'  // groupOrder can be a property name or a sorting function'
      },
      groups: [],
      items: dataSet.get(),
      currentPatient: null,
      activeTimelineResource: Session.get('activeTimelineResource'),
      activeTimelineResourceType: Session.get('activeTimelineResourceType'),
      activeTimelineResourceDescription: Session.get('activeTimelineResourceDescription'),
      query: {}
    }
    
    // if(this.props.birthdate){
    //   data.options.start = this.props.birthdate;
    // }

    if(get(currentPatient, 'birthDate')){
      data.options.start = moment(currentPatient.birthDate).format('YYYY-MM-DD');  // birthdate
      data.options.end = moment().format("YYYY-MM-DD"); // current date
      data.options.min = moment(currentPatient.birthDate).subtract(270, 'days').format('YYYY-MM-DD');  // birthdate - 9mo // conception date
      data.options.max = moment(currentPatient.birthDate).add(78, 'years').format('YYYY-MM-DD');  // birthdate +76 yrs  // estimated death date
    }

    var genderColor;

    if(get(currentPatient, 'gender')){
      if(get(currentPatient, 'gender') === 'male'){
        genderColor = '#d5ddf6';
      }
      if(get(currentPatient, 'gender') === 'female'){
        genderColor = '#ecdcde';
      }
    }

    if(Session.get('gender') === 'female'){
      genderColor = '#ecdcde';
    } else if (Session.get('gender') === "maleBabyBlue"){
      genderColor = '#d5ddf6';
    }

    if(Meteor.userId()){
      var continuityOfCare = get(Meteor.user(), 'profile.continuityOfCare', {});

      if(get(currentPatient, 'birthDate')){

        var inUtero = {
          mongoId: 'utero',
          id: 'utero',
          start: moment(currentPatient.birthDate).subtract(270, 'days').format('YYYY-MM-DD'),
          end: moment(currentPatient.birthDate).format('YYYY-MM-DD'),
          type: 'background',
          style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        }
        // in-utero
        if(!dataSet.get('utero')){
          dataSet.add(inUtero)   
        }

        // puberty
        var puberty = {
          mongoId: 'puberty',
          id: 'puberty',
          start: moment(currentPatient.birthDate).add(10, 'years').format('YYYY-MM-DD'),
          end: moment(currentPatient.birthDate).add(17, 'years').format('YYYY-MM-DD'),
          type: 'background',
          style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        }
        // in-utero
        if(!dataSet.get('puberty')){
          dataSet.add(puberty)   
        }

        // menarche
        var menarche = {
          mongoId: 'menarche',
          id: 'menarche',
          start: moment(currentPatient.birthDate).add(55, 'years').format('YYYY-MM-DD'),
          end: moment(currentPatient.birthDate).add(60, 'years').format('YYYY-MM-DD'),
          type: 'background',
          style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        }
        // in-utero
        if(!dataSet.get('menarche')){
          dataSet.add(menarche)   
        }


        // menarche
        var adjustedRisk = {
          mongoId: 'adjustedRisk',
          id: 'adjustedRisk',
          start: moment(currentPatient.birthDate).add(70, 'years').format('YYYY-MM-DD'),
          end: moment(currentPatient.birthDate).add(78, 'years').format('YYYY-MM-DD'),
          type: 'background',
          style: 'background-color: #cccccc; opacity: 0.5;'
        }
        // in-utero
        if(!dataSet.get('adjustedRisk')){
          dataSet.add(adjustedRisk)   
        }
        // // menarche
        // var menarcheDate = {
        //   start: '1988-07-04',
        //   type: 'background',
        //   style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        // }
        // dataSet.add(menarcheDate)   

        // // in-utero
        // dataSet.add({
        //   start: '1989-10-04',
        //   end: '1990-07-04',
        //   type: 'background',
        //   style: 'background-color: #ecdcde; opacity: 0.5;'
        // })   

        // // puberty
        // dataSet.add({
        //   start: '2000-07-04',
        //   end: '2007-07-04',
        //   type: 'background',
        //   style: 'background-color: #ecdcde; opacity: 0.5;'
        // })   

        // // pregnancy
        // dataSet.add({
        //   start: '2017-04-15',
        //   end: '2018-01-31',
        //   type: 'background',
        //   style: 'background-color: #ecdcde; opacity: 0.5;'
        // })   

      }

      let pointStyle = "filter: none; webkit-filter: none;";
      let rangeStyle = "background-color: white; border-color: lightgray;";

      if (Session.get('glassBlurEnabled')) {
        // pointStyle.filter = 'blur(3px)';
        // pointStyle.WebkitFilter = 'blur(3px)';
        pointStyle = "filter: blur(3px); webkit-filter: blur(3px);";
        rangeStyle = "background-color: white; border-color: lightgray; filter: blur(3px); webkit-filter: blur(3px);";
      }


      var filters = get(Meteor.user(), 'profile.filters');
  
      if(filters){
        data.query = {};
        if(filters.remove && filters.remove.length > 0){
          if(!data.query.$nor) data.query.$nor = [];
          filters.remove.forEach(function(term){
            if(term){
              data.query.$nor.push({ "code.text": { "$regex": "^" + term, "$options": "i" } })
            }
          });  
        }
  
        if(filters.mustHave && filters.mustHave.length > 0){
          if(!data.query.$or) data.query.$or = [];
          filters.mustHave.forEach(function(term){
            if(term){
              data.query.$or.push({ "code.text": { "$regex": "^" + term, "$options": "i" } })
            }
          })          
        }

        if(get(filters, 'sensitiveItems.substanceAbuse')){
          if(!data.query.$nor) data.query.$nor = [];
          // Additional info:
          // http://www.healthvermont.gov/sites/default/files/documents/2016/11/ADAP_ICD-10_ADAP_Approved_Conversion_Codes.pdf
          data.query.$nor.push({ "code.text": { "$regex": "^alcohol", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^cocain", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^opioid", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^hallucinogen", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^psychoactive substance", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^cannabis", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^abuse", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^dependence", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^intoxication", "$options": "i" } })
        }

        if(get(filters, 'sensitiveItems.mentalHealth')){
          if(!data.query.$nor) data.query.$nor = [];
          data.query.$nor.push({ "code.text": { "$regex": "^anxiety", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^adjustment disnorder", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^post-traumatic stress disnorde", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^depressive", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^disorder", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^personality", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^phobia", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^obsessional", "$options": "i" } })

          // TODO: filter explicit codes
          // https://www.simplepractice.com/blog/top-icd-10-codes-behavinoral-health-2017/  
        }
        if(get(filters, 'sensitiveItems.sexualHealth')){
          if(!data.query.$nor) data.query.$nor = [];
          data.query.$nor.push({ "code.text": { "$regex": "^neisseria", "$options": "i", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^gonorrhoeae", "$options": "i", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^gonnorrhoeae", "$options": "i", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^gonnorrhea", "$options": "i", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^chlamydia", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^syphilis", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^hepatitis", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^eGFR", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^HIV", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^AIDS", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^herpes", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^crabs", "$options": "i" } })
          data.query.$nor.push({ "code.text": { "$regex": "^chlamydia", "$options": "i" } })

          // TODO:  filter explicit codes
          // https://www.aapc.com/blog/32415-sti-screening-under-medicare/
        }
      }

      
      console.log('Parsing AllergyIntolerances for timeline...')
      AllergyIntolerances.find().forEach(function(allergy){
        if(allergy){          
          if(!dataSet.get(get(allergy, '_id'))){
            let newAllergy = {
              mongoId: get(allergy, '_id', ''),
              id: get(allergy, '_id', ''),
              content: '',
              group: 'Allergies',
              start: null,
              end: null,
              type: 'point',
              style: pointStyle
            }

            if(get(allergy, 'onsetDateTime')){
              newAllergy.start = get(allergy, 'onsetDateTime');
            } else if(get(allergy, 'onsetPeriod')){
              newAllergy.start = get(allergy, 'onsetPeriod.start');
            } else if (get(allergy, 'recordedDate')){
              newAllergy.start = get(allergy, 'recordedDate');
            } else if (get(allergy, 'onset')){
              newAllergy.start = get(allergy, 'onset');
            } else if (get(allergy, 'lastOccurance')){
              newAllergy.start = get(allergy, 'lastOccurance');
            }

            if(get(allergy, 'onsetPeriod.end')){
              newAllergy.end = get(allergy, 'onsetPeriod.end');
              newAllergy.style = rangeStyle
              newAllergy.type = 'range';
            }

            if(get(allergy, 'reaction[0].description')){
              newAllergy.content = get(allergy, 'reaction[0].description');
            } else if(get(allergy, 'reaction[0].manifestation[0].text')){
              newAllergy.content = get(allergy, 'reaction[0].manifestation[0].text');
            }

            if(newAllergy.start){
              dataSet.add(newAllergy)     
            }
          }
        }
      });  

      console.log('Parsing CarePlans for timeline...')
      CarePlans.find().forEach(function(carePlan){
        if(carePlan){
          if(!dataSet.get(get(carePlan, '_id'))){
            let newCarePlan = {
              mongoId: get(carePlan, '_id', ''),
              id: get(carePlan, '_id', ''),
              content: '',
              group: 'CarePlans',
              start: null,
              end: null,
              type: 'range',
              style: rangeStyle
            }


            if(get(carePlan, 'category[0].text')){
              newCarePlan.content = get(carePlan, 'category[0].text', '');
            }

            if(get(carePlan, 'description', '')){
              newCarePlan.content = get(carePlan, 'description', '');
            }

            // do we even have range data?
            if(get(carePlan, 'period')){
              // apparently we have something
              // so at least get us started
              if(get(carePlan, 'period.start')){
                newCarePlan.start = get(carePlan, 'period.start');
              }
              // if there's an end date, great!
              if(get(carePlan, 'period.end')){
                newCarePlan.end = get(carePlan, 'period.end');
              } else if(get(data, 'options.max')){
                // otherwise, do we have some actuarial data?
                // set to end-of-life
                newCarePlan.end = get(data, 'options.max');            
              } else {
                // otherwise, set to... today?
                newCarePlan.end = moment();            
              }
            } else {
              // we didn't get date range data, so we need to estimate based on conditions in the careplan
              
              if(get(carePlan, 'addresses')){
                // is earlier
                carePlan.addresses.forEach(function(conditionReference){
                  Conditions.find({'code.text': conditionReference.display}).map(function(condition){
                    newCarePlan.start = get(condition, 'onsetDateTime');
                    // otherwise, set to... today?
                    newCarePlan.end = moment();            
                    // if(moment(condition.onsetDateTime).isBefore(startDate)){
                    //   newCarePlan.start = condition.onsetDateTime;
                    // }
                  })
                })
              }
            }

            if(newCarePlan.start){
              dataSet.add(newCarePlan)     
            }

          }
        }
      })

      let conditionsQuery = {};
      if(data.query){
        conditionsQuery = data.query;
      }
      if(Session.get('hideEnteredInError')){          
        conditionsQuery.verificationStatus = {$nin: ["entered-in-error"]}  // unconfirmed | provisional | differential | confirmed | refuted | entered-in-error
      }
      console.log('Parsing Conditions for timeline...')
      Conditions.find(conditionsQuery).forEach(function(condition){
        if(condition){
          if(!dataSet.get(get(condition, '_id'))){
            let newCondition = {
              mongoId: get(condition, '_id', ''),
              id: get(condition, '_id', ''),
              group: 'Conditions',
              content: '',
              start: null,
              end: null,
              // content: get(condition, 'identifier[0].value') ? get(condition, 'identifier[0].value', '') : get(condition, 'code.coding[0].display', ''),
              // start: moment(get(condition, 'onsetDateTime')).format("YYYY-MM-DD"),
              type: 'point',
              style: pointStyle
            }

            if(get(condition, 'code.text')){
              newCondition.content = get(condition, 'code.text');
            } else if(get(condition, 'code.coding[0].display')){
              newCondition.content = get(allergy, 'code.coding[0].display');
            } else if(get(condition, 'identifier[0].value')){
              newCondition.content = get(allergy, 'identifier[0].value');
            }

            if(get(condition, 'onsetDateTime')){
              newCondition.start = get(condition, 'onsetDateTime');
            } 

            if(get(condition, 'abatementDateTime')){
              newCondition.end = get(condition, 'abatementDateTime');
              newCondition.style = rangeStyle
              newCondition.type = 'range';
            }


            if(newCondition.start){
              dataSet.add(newCondition);   
            }
          }
        }
      });  

      console.log('Parsing Immunizations for timeline...')
      Immunizations.find().forEach(function(immunization){
        if(immunization){
          if(!dataSet.get(get(immunization, '_id'))){

            let newImmunization = {
              mongoId: get(immunization, '_id', ''),
              id: get(immunization, '_id', ''),
              group: 'Immunizations',
              content: '',
              start: null,
              end: null,
              type: 'point',
              style: pointStyle
            }

            if(get(immunization, 'vaccineCode.text')){
              newImmunization.content = get(immunization, 'vaccineCode.text');
            }

            if(get(immunization, 'occurenceDateTime')){
              newImmunization.start = get(immunization, 'occurenceDateTime');
            } else if (get(immunization, 'date')){
              newImmunization.start = get(immunization, 'date');
            } else if (get(immunization, 'recorded')){
              newImmunization.start = get(immunization, 'recorded');
            }

            if(newImmunization.start){
              dataSet.add(newImmunization)     
            }
          }
        }
      });   

      console.log('Parsing MedicationOrders for timeline...')
      MedicationOrders.find().forEach(function(medicationOrder){
        //console.log('medicationOrder', medicationOrder);
        if(medicationOrder){
          if(!dataSet.get(get(medicationOrder, '_id'))){
            let newMedicationOrder = {
              group: 'MedicationOrders',
              mongoId: get(medicationOrder, '_id', ''),
              id: get(medicationOrder, '_id', ''),
              content: '',
              // id: get(medicationOrder, 'id') ? get(medicationOrder, 'id') : get(medicationOrder, '_id', ''),
              // content: contextText,
              // start: get(medicationOrder, 'dateWritten'),
              start: null,
              end: null,
              type: 'point',
              style: pointStyle
            }

            if(get(medicationOrder, 'dateWritten')){
              newMedicationOrder.start = get(medicationOrder, 'dateWritten');
            } 
            if(get(medicationOrder, 'dateEnded')){
              newMedicationOrder.end = get(medicationOrder, 'dateEnded');
              newMedicationOrder.style = rangeStyle
              newMedicationOrder.type = 'range';
            } 

            if(get(medicationOrder, 'medicationReference.display')){
              newMedicationOrder.content = get(medicationOrder, 'medicationReference.display');
            } else if(get(medicationOrder, 'medicationCodeableConcept.text')){
              newMedicationOrder.content = get(medicationOrder, 'medicationCodeableConcept.text');
            } else if(get(medicationOrder, 'reaction[0].description')){
              newMedicationOrder.content = get(medicationOrder, 'reaction[0].description');
            } else if(get(medicationOrder, 'identifier[0].value')){
              newMedicationOrder.content = get(medicationOrder, 'identifier[0].value');
            }

            if(newMedicationOrder.start){
              dataSet.add(newMedicationOrder);     
            }
          }
        }
      });  

      console.log('Parsing MedicationStatements for timeline...')
      MedicationStatements.find().forEach(function(medicationStatement){        
        if(medicationStatement){
          if(!dataSet.get(get(medicationStatement, '_id'))){
            let newMedicationStatement = {
              mongoId: get(medicationStatement, '_id', ''),
              id: get(medicationStatement, '_id', ''),
              group: 'MedicationStatements',
              content: '',
              start: null,
              end: null,
              type: 'point',
              style: pointStyle
              // id: get(medicationStatement, 'id') ? get(medicationStatement, 'id') : get(medicationStatement, '_id', ''),
              // content: get(medicationStatement, 'contained[0].code.coding[0].display', ''),
              // start: get(medicationStatement, 'effectiveDateTime', null),
            }

            if(get(medicationStatement, 'contained[0].code.coding[0].display')){
              newMedicationStatement.content = get(medicationStatement, 'contained[0].code.coding[0].display');
            }
  
            if(get(medicationStatement, 'effectiveDateTime')){
              newMedicationStatement.start = get(medicationStatement, 'effectiveDateTime');
            } else if (get(medicationStatement, 'effectivePeriod.start')){
              newMedicationStatement.start = get(medicationStatement, 'effectivePeriod.start');
            }
  
            if (get(medicationStatement, 'effectivePeriod.end')){
              newMedicationStatement.end = get(medicationStatement, 'effectivePeriod.end');
              newMedicationStatement.style = rangeStyle
              newMedicationStatement.type = 'range';
            }
  
            if(newMedicationStatement.start){
              dataSet.add(newMedicationStatement);     
            }
          }
        }
      });  



      console.log('Parsing Observations for timeline...')
      Observations.find(data.query).forEach(function(observation){
        if(observation){
          if(!dataSet.get(get(observation, '_id'))){
            let newObservation = {
              group: 'Observations',
              mongoId: get(observation, '_id', ''),
              id: get(observation, '_id', ''),
              content: '',
              start: null,
              end: null,
              // id: get(observation, 'id') ? get(observation, 'id') : get(observation, '_id', ''),
              // content: contextText.substring(0, 32), 
              // start: moment(get(observation, 'effectiveDateTime')).format('YYYY-MM-DD'),
              type: 'point',
              style: pointStyle
            }

            if(get(observation, 'code.text')){
              newObservation.content = get(observation, 'code.text');
            } else if(get(observation, 'code.coding[0].display')){
              newObservation.content = get(observation, 'code.coding[0].display');
            } 
            
            if(get(observation, 'valueQuantity.value')){
              newObservation.content = newObservation.content + ' - ' + get(observation, 'valueQuantity.value') 
            } else 
            if(get(observation, 'valueQuantity.unit')){
              newObservation.content = newObservation.content + ' - ' + get(observation, 'valueQuantity.unit') 
            } else
            if(get(observation, 'valueString')){
              newObservation.content = newObservation.content + ' - ' + get(observation, 'valueString') 
            } 
            newObservation.content = newObservation.content.substring(0, 32);

            if(get(observation, 'effectiveDateTime')){
              newObservation.start = get(observation, 'effectiveDateTime');
            } else if(get(observation, 'effectivePeriod')){
              newObservation.start = get(observation, 'effectivePeriod.start');
              newObservation.end = get(observation, 'effectivePeriod.end');
            } else {
              newObservation.start = get(observation, 'issued');    
              newObservation.pointStyle = "filter: none; webkit-filter: none; border-color: #42f49e;"; 
              newObservation.className = 'vis-item-issued';
            }

            if(newObservation.start){
              dataSet.add(newObservation);
            }
          }
        }      
      });  

      console.log('Parsing Procedures for timeline...')
      Procedures.find().forEach(function(procedure){        
        if(procedure){
          if(!dataSet.get(get(procedure, '_id'))){
            let newProcedure = {
              //content: get(procedure, 'identifier[0].value') ? get(procedure, 'identifier[0].value') : get(procedure, 'code.text'),
              mongoId: get(procedure, '_id', ''),
              id: get(procedure, '_id', ''),
              // id: get(procedure, 'id') ? get(procedure, 'id') : get(procedure, '_id', ''),
              content:  get(procedure, 'code.text'),
              group: 'Procedures',
              start: moment(get(procedure, 'performedDateTime')).format('YYYY-MM-DD'),
              type: 'point',
              style: pointStyle
            }

            if(get(procedure, 'code.text')){
              newProcedure.content = get(procedure, 'code.text');
            } else if (get(procedure, 'identifier[0].value')){
              newProcedure.content = get(procedure, 'identifier[0].value');
            }
            
            if(get(procedure, 'performedDateTime')){
              newProcedure.start = get(procedure, 'performedDateTime');
            } else if (get(procedure, 'performedPeriod.start')){
              newProcedure.start = get(procedure, 'performedDateTime.start');
            }

            if (get(procedure, 'performedPeriod.end')){
              newProcedure.end = get(procedure, 'performedDateTime.end');
            } else {
              // uh, we received data in a period, but didn't get an end date
              // and procedures generally aren't continuous?
              // so, we'll just add it as an end point?
              newProcedure.end = get(procedure, 'performedDateTime.start');
            }

            if(newProcedure.start){
              dataSet.add(newProcedure);
            }
          }
        }
      });    
    }

    // const now = moment().minutes(0).seconds(0).milliseconds(0);
    
    // create a data set with groups
    // const names = ['Conditions', 'MedicationOrders', 'Observations', 'Procedures', "CarePlans", "Allergies", "Immunizations"]

    // we want to use the preferred filter settings in the profile object
    const names = get(Meteor.user(), 'profile.filters.resourceTypes', 
      // but if we need to fall back to a default, we fall back to this list
      ['Conditions', 'MedicationOrders', 'Observations', 'Procedures', "CarePlans", "Allergies", "Immunizations"]);

    for (let g = 0; g < names.length; g++) {
      data.groups.push({ id: names[g], content: names[g] })
    }

    data.items = dataSet.get();

    // if(Session.get('glassBlurEnabled')){
    //   var timeLineEvents = {};
    //   // this.timeline = (<EventsTimeline events={timeLineEvents}/>)
    //   this.timeline = (<Timeline 
    //       selectHandler={ this.openDialog }
    //       changedHandler={this.preventRedraw }
    //       clickHandler={this.preventRedraw }
    //       options={this.data.options} 
    //       items={this.data.items} 
    //       groups={this.data.groups} 
    //       events={timeLineEvents}
    //       />)
    // }

    console.log('TimelineSidescrollPage.data', data)
    return data;
  }
  handleCloseDialog(){
    Session.set('timelineDialogOpen', false);
  }
  openDialog(props){
    console.log('openDialog', props)
    let activeTimelineItem = dataSet.get(props.items[0]);
    console.log('activeTimelineItem', activeTimelineItem);

    let activeTimelineRecord
    if(activeTimelineItem.group){
      activeTimelineRecord = Mongo.Collection.get(activeTimelineItem.group).findOne({_id: activeTimelineItem.mongoId})
      console.log('activeTimelineRecord', activeTimelineRecord)
      Session.set('activeTimelineResource', activeTimelineRecord);
      Session.set('activeTimelineResourceType', activeTimelineItem.group);
      Session.set('activeTimelineResourceDescription', activeTimelineItem.content);
    }
    
    Session.set('timelineDialogOpen', true);
    
    props.event.preventDefault();
    props.event.stopPropagation();
  }
  preventRedraw(props){
    if(props && props.event){
      props.event.preventDefault();
      props.event.stopPropagation();  
    }
  }
  render(){

    let catchActions = [
      // <FlatButton
      //   label="Send SMS Message"
      //   primary={true}
      //   keyboardFocused={true}
      //   onClick={this.sendSmsMessage}
      // />,
      <FlatButton
        label="Cancel"
        primary={true}
        onClick={this.handleCloseDialog}
      />
    ];

    let self = this;

    let dialogContent;
    let resourceType = ''
    let dialogTitle = ''
    switch (this.data.activeTimelineResourceType) {
      case 'CarePlans':
        resourceType = 'CarePlan'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <CarePlanDetail 
          showHints={false}
          careplan={ get(this, 'data.activeTimelineResource') }
          careplanId={ get(this, 'data.activeTimelineResource.id') }
        />        
        break;
      case 'Conditions':
        resourceType = 'Condition'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <ConditionDetail 
          showHints={false}
          showPatientInputs={false}
          condition={ get(this, 'data.activeTimelineResource') }
          conditionId={ get(this, 'data.activeTimelineResource.id') }
        />        
        break;
      case 'Observations':
        resourceType = 'Observation'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <ObservationDetail
          displayDatePicker={true} 
          displayBarcodes={false}
          showHints={false}
          showPatientInputs={false}
          observation={ get(this, 'data.activeTimelineResource') }
          observationId={ get(this, 'data.activeTimelineResource.id') }
        />
        break;

      case 'Immunizations':
        resourceType = 'Immunization'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <ImmunizationDetail
          displayDatePicker={true} 
          displayBarcodes={false}
          showHints={false}
          showPatientInputs={false}
          immunization={ get(this, 'data.activeTimelineResource') }
          immunizationId={ get(this, 'data.activeTimelineResource.id') }
        />
        break;

      case 'MedicationOrders':
        resourceType = 'MedicationOrder'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <MedicationOrderDetail
          displayDatePicker={true} 
          displayBarcodes={false}
          showHints={false}
          showPatientInputs={false}
          medicationOrder={ get(this, 'data.activeTimelineResource') }
          medicationOrderId={ get(this, 'data.activeTimelineResource.id') }
        />
        break;
        
      case 'Procedures':
        resourceType = 'Procedure'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <ProcedureDetail
          displayDatePicker={true} 
          displayBarcodes={false}
          showHints={false}
          showPatientInputs={false}
          procedure={ get(this, 'data.activeTimelineResource') }
          procedureId={ get(this, 'data.activeTimelineResource.id') }
        />
        break;

        

      default:
        break;
    }

    
    return(
      <div id="horizontalTimeline" style={{paddingTop: '64px', paddingBottom: '64px', backgroundColor: '#f3f3f3'}}>
        { this.timeline }
        <Dialog
            actions={catchActions}
            modal={false}
            open={this.data.timelineDialogOpen}
            // onRequestClose={this.handleCloseDialog}
          >
            <CardTitle title={dialogTitle} subtitle={resourceType} />
            <CardText>
              { dialogContent }
            </CardText>
          </Dialog>
      </div>
    );
  }
}
ReactMixin(TimelineSidescrollPage.prototype, ReactMeteorData);
export default TimelineSidescrollPage;

