  setToggleState(collection, isInputChecked){
    let toggleStates = Session.get('toggleStates');
    toggleStates[collection] = isInputChecked;
    console.log('toggleStates', toggleStates)
    Session.set('toggleStates', toggleStates);
  }
  setToggleExportState(collection, isInputChecked){
    let toggleExportStates = Session.get('toggleExportStates');
    toggleExportStates[collection] = isInputChecked;
    process.env.NODE_ENV === "verbose" && console.log('toggleExportStates', toggleExportStates)
    Session.set('toggleExportStates', toggleExportStates);
  }
