  singularizeResourceName(resourceTypeString){
    var singularized = '';
    switch (resourceTypeString) {
      case 'Binaries':          
        singularized = 'Binary';
        break;
      case 'Libraries':      
        singularized = 'Library';
        break;
      case 'SupplyDeliveries':      
        singularized = 'SupplyDelivery';
        break;
      case 'ImagingStudies':      
        singularized = 'ImagingStudy';
        break;        
      case 'FamilyMemberHistories':      
        singularized = 'FamilyMemberHistory';
        break;        
      case 'ResearchStudies':      
        singularized = 'ResearchStudy';
        break;        
      default:
        singularized = resourceTypeString.substring(0, resourceTypeString.length - 1)
        break;
    }

    return singularized;
  }
  exportCollection(collectionName){
    //alert("Not entirely implemented yet.");
    let self = this;

    console.log('Exporting the ' + collectionName + ' collection.')
    console.log('There are ' + Mongo.Collection.get(collectionName).find().count() + ' records.')

    console.log('Creating bundle...')
    var collectionBundle = {
      resourceType: "Bundle",
      entry: Mongo.Collection.get(collectionName).find().map(function(record){
        let recordId = record._id;
        delete record._document;
        delete record._id;
        
        let entry = {
          fullUrl: "/" + self.singularizeResourceName(collectionName) +"/" + recordId,
          resource: record
        }

        return entry;
      })
    }
 
    var dataString = 'data:text/csv;charset=utf-8,' + encodeURIComponent(JSON.stringify(collectionBundle, null, 2));  
    var downloadAnchorElement = document.getElementById('downloadAnchorElement');
    if(downloadAnchorElement){
      downloadAnchorElement.setAttribute("href", dataString );
  
      downloadAnchorElement.setAttribute("download", collectionName + ".fhir");
      downloadAnchorElement.click();
      // window.open('data:text/csv;charset=utf-8,' + escape(continuityOfCareDoc), '_self');    
    } else {
      console.log('Couldnt find anchor element.')
    }

  }
