  pluralizeResourceName(resourceType){
    var pluralized = '';
    switch (resourceType) {
      case 'Binary':          
        pluralized = 'Binaries';
        break;
      case 'Library':      
        pluralized = 'Libraries';
        break;
      case 'SupplyDelivery':      
        pluralized = 'SupplyDeliveries';
        break;
      case 'ImagingStudy':      
        pluralized = 'ImagingStudies';
        break;        
      case 'FamilyMemberHistory':      
        pluralized = 'FamilyMemberHistories';
        break;        
      case 'ResearchStudy':      
        pluralized = 'ResearchStudies';
        break;        
      default:
        pluralized = resourceType + 's';
        break;
    }

    return pluralized;
  }
  parseFacebookProfile(dataContent){
      // FACEBOOK ADDRESS BOOK
      if(dataContent.address_book){
        if(confirm('This appears to be a Facebook address book.  Would you like to import into the Persons collection?')){
          console.log('Importing Facebook address book...', dataContent.address_book);

          dataContent.address_book.address_book.forEach(function(entry){
            if(!Persons.findOne({'name.text': entry.name})){
              Persons.insert({
                resourceType: 'Person',
                name: [{
                  text: entry.name
                }]
              })  
            }
          })

        }
      }

      // FACEBOOK PROFILE!
      if(dataContent.profile){
        if(confirm('This appears to be a Facebook profile.  Would you like to import and override your current settings?')){
          console.log('Importing Facebook profile...', dataContent.profile);

          let currentUser = Meteor.user();

          if(get(currentUser, 'profile')){

            // NAME
            if(get(dataContent, 'profile.name')){
              let nameText = get(dataContent, 'profile.name');
              currentUser.profile.name.text = nameText;

              let nameArray = nameText.split(' ');

              Meteor.users.update({_id: Meteor.userId()}, {$set: {
                'profile.name.family': nameArray[1],
                'profile.name.text': get(dataContent, 'profile.name')
              }, $addToSet: {
                'profile.name.given': nameArray[0]
              }})
            }

            // ADDRESS
            if(get(dataContent, 'profile.address')){
              let facebookAddres = get(dataContent, 'profile.address');

              Meteor.users.update({_id: Meteor.userId()}, {$unset: {
                'profile.locations.home.address.line': []
              }})

              Meteor.users.update({_id: Meteor.userId()}, {$set: {
                'profile.locations.home.address.city': facebookAddres.city,
                'profile.locations.home.address.country': facebookAddres.country_code,
                'profile.locations.home.address.state': facebookAddres.region,
                'profile.locations.home.address.postalCode': facebookAddres.zipCode
              }, $addToSet: {
                'profile.locations.home.address.line': facebookAddres.street
              }})
            }

            // GENDER
            if(get(dataContent, 'profile.gender.gender_option')){
              Meteor.users.update({_id: Meteor.userId()}, {$set: {
                'profile.gender': currentUser.profile.gender.gender_option
              }})
            }

            // BIRTHDATE
            if(get(dataContent, 'profile.birthday')){
              currentUser.profile.birthDate = new Date(
                get(dataContent, 'profile.birthday.year'),
                get(dataContent, 'profile.birthday.month') - 1,
                get(dataContent, 'profile.birthday.day')
              );

              Meteor.users.update({_id: Meteor.userId()}, {$set: {
                'profile.birthDate': currentUser.profile.birthDate
              }})
            }

            // TELECOM
            if(get(dataContent, 'profile.emails.emails')){
              dataContent.profile.emails.emails.forEach(function(emailAddress){
                Meteor.users.update({_id: Meteor.userId()}, {$addToSet: {
                  'profile.telecom': {
                    'system': 'email',
                    'value': emailAddress
                  }
                }})  
              })
            }

            // MARITAL STATUS
            if(get(dataContent, 'profile.relationship.status')){
              Meteor.users.update({_id: Meteor.userId()}, {$set: {
                'profile.maritalStatus': get(dataContent, 'profile.relationship.status')
              }})
            }


            // CONTACT POINTS
            if(get(dataContent, 'profile.family_members')){
              dataContent.profile.family_members.forEach(function(familyMember){
                Meteor.users.update({_id: Meteor.userId()}, {$addToSet: {
                  'profile.contact': {
                    'relationship': [{text: familyMember.relation}],
                    'name': {'text': familyMember.name}
                  }
                }})  

                if(!RelatedPersons.findOne({'name.text': familyMember.name})){
                  RelatedPersons.insert({
                    name: {
                      text: familyMember.name
                    }, 
                    relationship: {
                      text: familyMember.relation
                    }
                  })  
                }
              })
            }
          }
        }
      }

  }
