import { CardMedia, CardText, CardTitle, CardHeader } from 'material-ui/Card';
import { GlassCard, VerticalCanvas, FullPageCanvas, Glass, DynamicSpacer } from 'meteor/clinical:glass-ui';
import { Col, Grid, Row } from 'react-bootstrap';

import React from 'react';
import { ReactMeteorData } from 'meteor/react-meteor-data';
import ReactMixin from 'react-mixin';
import { browserHistory } from 'react-router';

import { get, has, uniq, uniqBy } from 'lodash';
import { moment } from 'meteor/momentjs:moment';

import { Session } from 'meteor/session';
import { ObservationsTable } from 'meteor/clinical:hl7-resource-observation';
import { Line } from 'nivo'
import { render } from 'react-dom'

import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

// import { InlineMath, BlockMath } from 'react-katex';
// import 'katex/dist/katex.min.css';
Session.setDefault('currentObservationType', 'Pulse')

export class ObservationsGraphPage extends React.Component {
  constructor(props) {
    super(props);
  }
  getMeteorData() {

    let imgHeight = (Session.get('appHeight') - 210) / 3;

    let data = {
      chart: {
        width: Session.get('appWidth') * 0.667,  
        height: Session.get('appHeight') - 168
      },
      style: {              
        title: Glass.darkroom(),
        subtitle: Glass.darkroom(),
        tableCol: {
          display: 'block',
          marginBottom: '20px'
        }
      },
      organizations: {
        image: "/pages/provider-directory/organizations.jpg"
      },
      bmi: {
        height: 0,
        weight: 0
      },
      uniqueCodes: [],
      currentObservationType: Session.get('currentObservationType')
    };

    data.style.indexCard = Glass.darkroom(data.style.indexCard);

    // The `_.property` iteratee shorthand.
    // _.uniqBy([{ 'x': 1 }, { 'x': 2 }, { 'x': 1 }], 'x');
    // => [{ 'x': 1 }, { 'x': 2 }]

    let observations = Observations.find().map(function(record){
      return get(record, 'code.text');
    })
    if(observations){
      let uniqueCodes = uniq(observations)
      //console.log('Unique Observation Codes: ', uniqueCodes)

      data.uniqueCodes = uniq(observations);
    }

    if(Session.get('appHeight') > 1200){
      data.style.page = {
        top: '50%',
        transform: 'translateY(-50%)',
        position: 'relative'
      }
    }

    if(Observations.find({'code.text': data.currentObservationType }).count() > 0){
      let recentPulse = Observations.find({'code.text': data.currentObservationType }, {sort: {issued: 1}}).fetch()[0];
      // data.bmi.weight = get(recentPulse, 'valueQuantity.value', 0);
    }

    if(Session.get('appWidth') > Session.get('appHeight')){
      data.chart.width = Session.get('appWidth');
    }

    if(['iPhone'].includes(window.navigator.platform)){
      data.style.tableCol.display = 'none';
      data.chart.width = Session.get('appWidth');
    }



    if(process.env.NODE_ENV === "test") console.log("ObservationsGraphPage[data]", data);
    return data;
  }
  selectObservationType(event, value){
    console.log('Selected a new observation type: ', value.props.children)
    Session.set('currentObservationType', value.props.children)
  };
  render() {

    let menuItems = [];
    this.data.uniqueCodes.forEach(function(code, integer){
      menuItems.push(<MenuItem key={integer} value={integer} style={{fontSize: '130%'}}>{code}</MenuItem>)
    });

    let currentSelectIndex = this.data.uniqueCodes.indexOf(get(this, 'data.currentObservationType'))

    
    return (
      <div id='indexPage'>
        <FullPageCanvas>
          <Col md={4} style={ this.data.style.tableCol } >
            <GlassCard height='auto' >
              <CardTitle 
                title="Vital Signs"
                titleStyle={{fontSize: '240%'}}
                subtitleStyle={{fontSize: '180%'}}
                />
              <Select
                value={ currentSelectIndex }
                onChange={this.selectObservationType}
                style={{
                  position: 'absolute',     
                  right: '20px',
                  top: '20px',
                  marginRight: '20px',
                  width: '240px',
                  fontSize: '150%'
                }}
                inputProps={{
                  name: 'age',
                  id: 'age-simple',
                }}
              >
                { menuItems }
                {/* <MenuItem value={2} style={{fontSize: '130%'}}>Temperature</MenuItem>
                <MenuItem value={3} style={{fontSize: '130%'}}>Blood Pressure</MenuItem>
                <MenuItem value={4} style={{fontSize: '130%'}}>Respiration</MenuItem> */}
              </Select>             
              <CardText style={{fontSize: '180%'}}>
                <Row>
                  <ObservationsTable 
                    query={{$or: [{'code.text': get(this, 'data.currentObservationType')}]}} 
                    multiline={true}
                    />
                </Row>
              </CardText>
            </GlassCard>
          </Col>
          <Col md={8} style={{marginBottom: '80px'}}>
            <GlassCard height='auto'>
              <Line
                width={this.data.chart.width}
                height={this.data.chart.height}
                curve='cardinal'
                // colorBy="color"
                colors={['#35B6ED']}
                data={[
                  {
                    "id": get(this, 'data.currentObservationType'),
                    "color": "#35B6ED",
                    "data": Observations.find({$or: [{'code.text': get(this, 'data.currentObservationType')}]}, {sort: {issued: 1}}).map(function(observation, index){
                      return {
                        key: index,
                        // color: "#35B6ED",
                        x: moment(get(observation, 'issued')).format('MMM DD, HH'),
                        y: get(observation, 'valueQuantity.value')
                      }
                    })
                  }
                ]}
                margin={{
                  "top": 50,
                  "right": 110,
                  "bottom": 50,
                  "left": 60
                }}
                minY="auto"
                stacked={true}
                axisBottom={{
                  "orient": "bottom",
                  "tickSize": 5,
                  "tickPadding": 5,
                  "tickRotation": 0,
                  // "legend": "hour of day",
                  "legendOffset": 36,
                  "legendPosition": "center"
                }}
                axisLeft={{
                  "orient": "left",
                  "tickSize": 5,
                  "tickPadding": 5,
                  "tickRotation": 0,
                  // "legend": "pulse (bps)",
                  "legendOffset": -40,
                  "legendPosition": "center"
                }}
                dotSize={10}
                dotColor="#35B6ED"
                dotBorderWidth={2}
                dotBorderColor="#ffffff"
                enableDotLabel={true}
                dotLabel="y"
                dotLabelYOffset={-12}
                animate={true}
                motionStiffness={90}
                motionDamping={15}
                legends={[
                  {
                    "anchor": "bottom-right",
                    "direction": "column",
                    "translateX": 100,
                    "itemWidth": 80,
                    "itemHeight": 20,
                    "symbolSize": 12,
                    "symbolShape": "circle"
                  }
                ]}
              />
              <DynamicSpacer />
              {/* <div style={{width: '100%', textAlign: 'center'}}>
                <h1>BMI = {this.data.bmi.weight} kg / {this.data.bmi.height} cm / {this.data.bmi.height} cm  * 10,000 = {bmi} </h1>
                <a href="https://www.cdc.gov/nccdphp/dnpao/growthcharts/training/bmiage/page5_1.html">Centers for Disease Control - Body Mass Index Calculations</a>
              </div> */}
            </GlassCard>
          </Col>
        </FullPageCanvas>
      </div>
    );
  }




  openLink(url){
    console.log("openLink", url);
    browserHistory.push(url);
  }
}



ReactMixin(ObservationsGraphPage.prototype, ReactMeteorData);

export default ObservationsGraphPage;